// /*--------------------------------------------------------------
// # Navigation
// --------------------------------------------------------------*/

// /* Links */

a {
  color: #fff;

  &:visited {
    color: #fff9c0;
  }
  &:hover,
  &:focus,
  &:active {
    color: #fff9c0;
  }
  &:focus {
    outline: thin dotted;
  }
  &:hover,
  &:active {
    outline: 0;
  }
}

