.site-header {
  position: absolute;
  top: 0;
}

.search-input input {
  width: 0px;
  transition: all 1s ease-in-out;
  display: inline-block;
}
.search-input.show input {
  width: 100px;
  opacity: 1;
}

// green color nav bar for unique pages

.single ul {
  color: $color__text-screen;
  text-shadow: none;
}

.archive ul {
  color: $color__text-screen;
  text-shadow: none;
}

.blog ul {
  color: $color__text-screen;
  text-shadow: none;
}
.error404 ul {
  a {
    text-shadow: none;
    color: $color__text-screen;
  }
}
